@charset "utf-8";
.marginTopDiv {
  height: 4.075rem;
}
.slider {
  width: 100%;
  height: 10.5rem;
  position: relative;
  overflow: hidden;
  z-index: -100;
}
.slider__slide {
  -webkit-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 10.5rem;
  max-height: 10.5rem;
  background-size: cover;
  background-position: center;
  z-index: 1;
  opacity: 0;
}
.slider__slide span {
  display: block;
  width: 50%;
  height: 100%;
  position: absolute;
  z-index: 2;
}
.slider__slide .goleft {
  left: 0;
  top: 0;
}
.slider__slide[data-active="true"] {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  z-index: 100;
  opacity: 1;
}
.slider__next,
.slider__previous {
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  color: rgba(255, 255, 255, 0.6);
  z-index: 200;
  position: absolute;
  height: 10.5rem;
}
.slider__next i,
.slider__previous i {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}
.slider__next:hover,
.slider__previous:hover {
  color: rgba(255, 255, 255, 0.8);
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}
.slider__next {
  right: 20px;
}
.slider__next i {
  -webkit-transform: translateX(-100%) translateY(-50%);
      -ms-transform: translateX(-100%) translateY(-50%);
          transform: translateX(-100%) translateY(-50%);
}
.slider__previous {
  left: 20px;
}
.fa-4x {
  font-size: 1.5rem;
}



